.card {
    width: 100%;
    max-width: 300px;
    height: 520px;
    border-radius: 36px;
    overflow: hidden;
    position: relative;
    a {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        text-decoration-line: none;
        z-index: 2;
    }
    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.12) 48.88%, rgba(0, 0, 0, 0.48) 74.43%, rgba(0, 0, 0, 0.8) 100%);
    }
}
.image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.title {
    position: absolute;
    box-sizing: border-box;
    margin: 0;
    padding: 0 10px;
    bottom: 39px;
    left: 0;
    width: 100%;
    height: 85px;
    color: #fff;
    z-index: 2;
    text-align: center;
    font-weight: 800;
    font-size: 36px;
    line-height: 110%;
    overflow: hidden;
}